import React from 'react';
import './mypalate.css'
import { Link } from 'react-router-dom'

const MyPalate = (props) => {
    return (
        <>
            <div className="mypalateMain">
                <div class="jumbotron jumbotron-fluid">
                    <div class="container">
                        <h1 class="display-4">Working with MyPalate</h1>
                        <p class="lead">Almost one year now of everything from front-end development to machine learning</p>
                    </div>
                </div>
                <h1>About my experience with MyPalate</h1>
                <section className="aboutmypalateSection">
                    <h4>What do you do at MyPalate?</h4>
                    <p>I work very much like a full stack developer at MyPalate. I have done front end development in React, used SQL, TensorFlow, and even created Discord Bots.</p>
                    <h4>How long have you been with MyPalate?</h4>
                    <p>I started around mid-way through 2020 so it has been about a year now.</p>
                    <h4>What soft skills have you learned with MyPalate?</h4>
                    <p>MyPalate is where I learned how to use a project board, do weekly sprints, and talk through bugs and errors in a close (virtual) environement with other developers as well as designers.</p>
                    <h4>Are you going to keep doing it?</h4>
                    <p>The people at MyPalate are great so I have no reason to stop!</p>
                </section>
                <br></br>
                <h1>Skills</h1>
                <div className="skillsTags">
                    <a href="https://www.tensorflow.org/"><img src="https://pbs.twimg.com/profile_images/1103339571977248768/FtFnqC38.png" alt="litIcon" style={{ borderRadius: "50%", width: "200px" }}></img></a>
                    <a href="https://discord.com/developers/docs/intro"><img src="https://top.gg/images/dblnew.png" alt="storybookIcon" style={{ borderRadius: "50%", width: "200px" }}></img></a>
                    <a href="https://reactjs.org/"><img src="data:image/png;base64," alt="reactIcon" style={{ borderRadius: "50%", width: "200px" }}></img></a>
                    <a href="https://www.postgresql.org/docs/"><img src="https://cdn.worldvectorlogo.com/logos/postgresql-inc.svg" alt="googleOptimizeIcon" style={{ borderRadius: "50%", width: "200px" }}></img></a>
                    <a href="https://trello.com/en-US"><img src="https://i.pinimg.com/280x280_RS/0f/b8/e6/0fb8e676a1cd0eae9b0f7ea862c40f93.jpg" alt="litIcon" style={{ borderRadius: "50%", width: "200px" }}></img></a>
                </div>
                <br></br>
            </div>
        </>
    );
}

export default MyPalate;